import { __ } from "@wordpress/i18n";
import AdvancedSwitcher from "../common/AdvancedSwitcher";
import Container from "../common/Container";
import { useDispatch, useSelector } from "react-redux";
import { updatePerformance } from "../features/performance/index";
import { actions } from "../features/Alert/AlertSlice";
import googleFonts from "../../../../../../../src/components/premium-fonts";
import Select from 'react-select';

const Performance = () => {
    const settings = useSelector((state) => state.performanceStates.data);
    const dispatch = useDispatch();
    const onChangeData = async (key, value) => {
        const updatedData = { ...settings };
        updatedData[key] = value;
        dispatch(updatePerformance(updatedData));
        const body = new FormData();

        body.append("action", "pb-panel-update-performance-options");
        body.append("nonce", PremiumBlocksPanelData.nonce);
        body.append("value", JSON.stringify(updatedData));

        try {
            const response = await fetch(PremiumBlocksPanelData.ajaxurl, {
                method: "POST",
                body,
            });
            console.log(response);
            if (response.status === 200) {
                const { success, data } = await response.json();
                if (success && data.setting) {
                    dispatch(
                        actions.createAlert({
                            message: __("Settings saved.", "premium-blocks-for-gutenberg"),
                            type: "success",
                        })
                    );
                }
            }
        } catch (e) {
            console.log(e);
            dispatch(
                actions.createAlert({
                    message: __("An unknown error occurred.", ""),
                    type: "error",
                })
            );
        }
    };

    const selectedGoogleFonts = settings?.["premium-google-fonts"] || [];
    const options = [
        ...Object.entries(googleFonts).map(([key, value]) => { return { value: key, label: key } })
    ];
    const customStyles = {
        control: (provided) => ({
            ...provided,
            cursor: 'pointer',
        }),
    }

    return (
        <Container>
            <div className="pb-settings">
                <div className="pb-setting-options">
                    <AdvancedSwitcher
                        label={__(
                            "Load Google Fonts Locally",
                            "premium-blocks-for-gutenberg"
                        )}
                        onChange={(checked) =>
                            onChangeData("premium-load-fonts-locally", checked)
                        }
                        checked={settings?.["premium-load-fonts-locally"] || false}
                        description={__(
                            "This option will be used to load google fonts locally in your website.",
                            "premium-blocks-for-gutenberg"
                        )}
                        childOption={{
                            label: __(
                                "Preload Local Fonts",
                                "premium-blocks-for-gutenberg"
                            ),
                            onChange: (checked) =>
                                onChangeData("premium-preload-local-fonts", checked),
                            checked: settings?.["premium-preload-local-fonts"] || false,
                            description: __(
                                "This option will be used to preload local fonts in your website.",
                                "premium-blocks-for-gutenberg"
                            )
                        }}
                    />
                    <AdvancedSwitcher
                        label={__(
                            "Allow Only Selected Fonts",
                            "premium-blocks-for-gutenberg"
                        )}
                        onChange={(checked) =>
                            onChangeData("premium-enable-allowed-fonts", checked)
                        }
                        description={__(
                            "Use this option to show only limited number of fonts in the block settings.",
                            "premium-blocks-for-gutenberg"
                        )}
                        checked={settings?.["premium-enable-allowed-fonts"] || false}
                        style={{ marginBottom: "0", border: "none" }}
                    />
                    {settings?.["premium-enable-allowed-fonts"] && (
                        <div className="premium-blocks__base-control">
                            <div style={{ flex: 1 }}>
                                <Select
                                    value={options.filter(
                                        (obj) =>
                                            selectedGoogleFonts.includes(
                                                obj.value
                                            )
                                    )}
                                    isMulti
                                    options={options}
                                    isSearchable={true}
                                    onChange={(options) =>
                                        onChangeData("premium-google-fonts", options.map((option) => option.value))
                                    }
                                    styles={customStyles}
                                />
                            </div>
                        </div>
                    )}
                </div>
            </div>
        </Container>
    );
}

export default Performance;